@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
:root{
  --primary-white: #ffffff;
  --primary-orange: #f9ab00;
  --primary-dark: rgb(52, 58, ,64);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-black: #000000;
}
body {
  font-family: "Roboto Condensed", sans-serif !important;
}
/****************************** NAVBAR ***************************/

nav {
  z-index: 2;
}
.logo {
  width: 6.25rem;
  height: 3.125rem;
}

nav a.nav-link {
  
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li{
  text-transform: uppercase;
}

.navbar-nav > li> a{
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #ffffff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgb(52, 58, ,64);
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand{
  max-width: 17.25rem;
  padding: 0 !important;
  /*height: 6rem !important;*/
}

@media(max-width: 768px) {
  .navbar-brand{
    width: 7rem;
  }
}


.navbar-nav > li > a.active, .navbar-nav > li > a:focus{
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover{
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}


/********** HEADER **********/
.header-wraper {
  position: relative;
  background: url(/static/media/portfolio-background.f5d38ec5.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 1;
}

.main-info h1{
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}

/********* TYPED TEXT **********/
.typed-text {
  font-size: 2rem;
  color: #ffffff;
  color: var(--primary-white);
}

@media(max-width: 768px){
  .main-info h1{
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}


/******* HEADER MAIN OFFER BUTTON ******/
.btn-main-offer {
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #ffffff;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color: #ffffff;
  color: var(--primary-white);
  transition: .2s ease-in-out;
}

/******* Particles ******/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media(max-width:768px){
  .particles-canvas {
    display: none;
  }
}


/*********************** ABOUT ME ****************/
.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}
.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
}

.about-heading {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}

@media(max-width: 468px){
  .profile-img {
    width: 10rem;

  }
}


/**************** SERVICES ********************/
.services {
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-grey);
}

.services h1{
  color: rgb(52, 58, ,64);
  color: var(--primary-dark);
  text-transform: uppercase;
}



/****************************** EXPEREINCE ***************************/
.experience {
  padding: 0.7rem;
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange);
}

.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}


.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;;
  border: 0.3125rem solid rgb(52, 58, ,64);;
  border: 0.3125rem solid var(--primary-dark);
  background: #f9ab00;
  background: var(--primary-orange);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: #666666;
  color: var(--primary-dark-grey);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: #ef4035;
  color: var(--primary-red);
  font-weight: 900;
}

/*------------ MEDIA ------------------*/
@media(max-width: 768px) {
  .experience .container {
    padding-left: 0;
  }
  
    .experience-wrapper::before {
      left: 0.5rem;
      margin-bottom: 1.875rem;
    }
    .timeline-block-right {
      float: none;
      width: 100%;
    }
    
    .timeline-block-left {
      float: none;
      direction: ltr; 
      width: 100%;
    }
  }

  /****************************** PROJECTS ***************************/
  .portfolio-wrapper {
    background: #ffffff;
    background: var(--primary-white);
    padding: 3rem 0;
  }
  
  .portfolio-wrapper h1 {
    color: #f9ab00;
    color: var(--primary-orange);
  }
  
  .portfolio-image {
    width: 15rem;
    height: 8rem;
    border: 1px solid #f9ab00;
    border: 1px solid var(--primary-orange);
    padding: 0 0.5rem;
  }
  
  .portfolio-image-box {
    position: relative;
    margin: 1rem;
    
  }

  .overflow {
    position: absolute;
     opacity: 0;
     background: #000000;
     background: var(--primary-black);
     width: 15rem;
     height: 8rem;
     top: 0;
    border: 1px solid #ef4035;
    border: 1px solid var(--primary-red);
    cursor: pointer;
  }
  
  .portfolio-image-box:hover .overflow {
    opacity: 60%;
    transition: 0.3s ease-in-out;
  }
  
  .portfolio-icon {
    position: absolute;
    opacity: 0;
    margin-left: -8rem;
    margin-top: 3.6rem;
    color: #f9ab00;
    color: var(--primary-orange);
    font-size: 1.5rem;
  }
  
  .portfolio-image-box:hover .portfolio-icon {
    opacity: 70%;
    transition: 0.3s ease-in-out;
    cursor: pointer;
  }

  /*------------ OVERFLOW BOX ------------------*/
.portfolio-image-popupbox {
  width: 45rem;
  height: 25rem;
  padding: 0 0.5rem;
}

.hyper-link {
  cursor: pointer; 
  color: #f9ab00; 
  color: var(--primary-orange);
}

.hyper-link:hover {
  color: #ef4035;
  color: var(--primary-red);
}

/*------------ MEDIA ------------------*/
@media(max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}

@media(max-height: 570px) {
.popupbox-wrapper {
  height: 100%;
}
  .portfolio-image-popupbox {
    width: 50%;
  }
}

@media(max-height: 280px) {
  .popupbox-wrapper {
    height: 90%;
  }
  .portfolio-image-popupbox {
    width: 30%;
  }
}



/****************************** SKILLS ***************************/
.testimonials {
  
  background-size: cover;
  background-color: #000000;
  background-color: var(--primary-black);
  background: #f4f4f4;
  background: var(--primary-light-grey);
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
}

.testimonials h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 3rem 0;
  text-align: center;
  text-transform: uppercase;
}



.testimonials-content p
{
  font-size:20px;
  color: #000000;
  color: var(--primary-black);
  display:inline;
}


.orange {
  color: #f9ab00 !important;
  color: var(--primary-orange) !important;
}

/************************* CONTACTS ***************/
.contacts {
  background: #f4f4f4;
  background: var(--primary-light-grey);
  padding: 3rem 0;
}

.contacts h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: #f4f4f4;
  color: var(--primary-light-grey) ;
  font-size: 1.2rem;
}

/********************* INPUTS **************/
.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: #f9ab00;
  color: var(--primary-orange);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: #ffffff;
  color: var(--primary-white);
  border-bottom: 0.125rem solid #f4f4f4;
  border-bottom: 0.125rem solid var(--primary-light-grey);
}

.contacts textarea {
  height: 12.2rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: #666666;
  background: var(--primary-dark-grey);
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: #f9ab00;
  background: var(--primary-orange);
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: #ffffff;
  color: var(--primary-white);
  background: #f33501;
  background: var(--primary-hover-red);
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: var(--primary-green);
}

/*---------------- MEDIA -----------------------*/
@media(max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }
  
  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}

/************************ FOOTER COMPONENT *********************************/
.footer {
  background: #000000;
  background: var(--primary-black);
  color: #666666;
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color: #f9ab00;
  color: var(--primary-orange);
  cursor:pointer;
}

.footer p {
  margin-bottom: 0;
}
